import { BusinessActionTypes } from './constants';

type UserData = {
    id: number;
    email: string;
    username: string;
    firstName: string;
    lastName: string;
    role: string;
    token: string;
};

// Define the action types for Business
export type BusinessAction = {
    type: BusinessActionTypes;
    payload: any; // Use 'any' or define specific payload types as needed
};

// Common success action creator
export const businessApiResponseSuccess = (actionType: string, data: UserData | {}): BusinessAction => ({
    type: BusinessActionTypes.BUSINESS_LIST_SUCCESS,
    payload: { actionType, data },
});

// Common error action creator
export const businessApiResponseError = (actionType: string, error: string): BusinessAction => ({
    type: BusinessActionTypes.BUSINESS_LIST_ERROR,
    payload: { actionType, error },
});

// Action creator for fetching the business list
export const businessList = (): BusinessAction => ({
    type: BusinessActionTypes.BUSINESS_LIST,
    payload: {}, // This can be customized if you want to pass more specific data
});

export const resetBusiness = (): BusinessAction => ({
    type: BusinessActionTypes.RESET,
    payload: {},
});
