import { all, fork, put, takeEvery, call } from 'redux-saga/effects';
import { SagaIterator } from '@redux-saga/core';

// apicore
import { APICore, setAuthorization } from '../../helpers/api/apiCore';

// actions
import { businessApiResponseError, businessApiResponseSuccess } from './actions';

// constants

import { BusinessActionTypes } from './constants';

const api = new APICore();

function* businessList(): SagaIterator {
    try {
        const response = yield call(businessList);
        console.log(response);
        api.setLoggedInUser(null);
        setAuthorization(null);
        yield put(businessApiResponseSuccess(BusinessActionTypes.BUSINESS_LIST, {}));
    } catch (error: any) {
        yield put(businessApiResponseError(BusinessActionTypes.BUSINESS_LIST, error));
    }
}

export function* watchBusinessList() {
    yield takeEvery(BusinessActionTypes.BUSINESS_LIST, businessList);
}

function* businessSaga() {
    yield all([fork(watchBusinessList)]);
}

export default businessSaga;
